* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: rgba(223, 220, 220, 0.5);
}

.test-table {
  position: absolute;
  top: 50%;
  left: 70%;
  transform: translate(-70%, -50%);
  width: 500px;
  height: fit-content;
  border: 1px solid rgba(0, 0, 0, .5);
  padding: 20px;
  border-radius: 15px;
  background: rgba(254, 254, 254, 0.5);
  box-shadow: 0 5px 15px rgba(0, 0, 0, .2);
}


.test-table h2 {
  margin-bottom: 10px;
}

.test-table .answer {
  list-style: none;
  padding: 0 0 5px 10px;
}

.test-table .button-group {
  display: flex;
  justify-content: space-between;
  padding: 10px 0 0;
}


.test-table .button-group button {
  padding: 3px 10px;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  background: #e0dddd;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, .2);
  border: 1px solid rgba(0, 0, 0, .1);
}

.scoreResult {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 250px;
  height: 250px;
  background: red;
}

.calculateScore {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 150px;
  height: 150px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
}

/************** Navigation *****************/
.nav {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  background: rgb(255, 255, 255);
  padding: 0px 100px;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .1);
}

.nav .nav-menu {
  display: flex;
}

.nav .nav-menu li {
  list-style: none;
  padding: 5px 20px;
  color: #000;
  font-weight: bold;
  position: relative;
  cursor: pointer;
}

.nav .nav-menu li:before {
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: #000;
  transform-origin: left;
  transform: scale(0);
  transition: .5s;
}

.nav .nav-menu li:hover:before {
  transform: scale(1);
  transform-origin: right;
}

.nav-login-signin {
  display: flex;
}

.nav-login-signin button {
  padding: 7px 15px;
  background: #cc67eb;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 15px;
  color: #000;
  box-shadow: 0 0 15px rgba(0, 0, 0, .2);
  cursor: pointer;
}

.nav-login-signin button:nth-child(2) {
  margin-left: 15px;
}

/********** Information Box *******************/

.info-box {
  position: absolute;
  top: 50%;
  left: 70%;
  transform: translate(-70%, -50%);
  width: 560px;
  height: fit-content;
  border: 1px solid rgba(0, 0, 0, .5);
  padding: 20px;
  border-radius: 20px;
  background: #fff;
}

.info-box p {
  text-indent: 15px;
  font-size: 22px;
  text-align: justify;
  padding: 20px 0px;
}

.info-box .info-box-button {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 0px 30px;
}

.info-box .info-box-button button {
  padding: 7px 15px;
  font-weight: bold;
  background: #29ed71;
  border: 1px solid rgba(0, 0, 0, .5);
  border-radius: 5px;
  cursor: pointer;
}

.info-box .info-box-button button:nth-child(1) {
  background: rgb(255, 255, 255);
}

/**********Footer*********/

.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: fit-content;
  background: rgb(255, 255, 255);
  text-align: center;
  overflow: hidden;
  box-shadow: 0 -5px 15px rgba(0, 0, 0, .1);
}

.footer::before {
  content: '';
  position: absolute;
  background: rgb(227 150 227 / 50%);
  left: 20%;
  width: 100px;
  height: 100px;
  clip-path: circle(25.0% at 42% 2%);
}

.footer::after {
  content: '';
  position: absolute;
  top: 1%;
  background: rgb(255 0 255 / 45%);
  width: 100px;
  height: 100px;
  clip-path: circle(15.0% at 41% 27%);
  right: 20%;
}

/***** Sidebar ********/

.side-bar {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  height: fit-content;
  padding: 20px;
  background: #fff;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  box-shadow: 2px 0 15px rgba(0, 0, 0, 0.2);
}

.side-bar .side-bar-content {
  padding: 15px 0;
}


.side-bar .side-bar-content li {
  list-style: none;
  padding: 10px 0px 10px 10px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  transition: padding .5s;
  cursor: pointer;
}

.side-bar .side-bar-content li:hover {
  font-weight: bold;
  padding: 10px 0px 10px 20px;
}

/************** Enter question form *************/
/* // "predeploy": "npm run build",
// "deploy": "gh-pages -d build" */
.enter-question-form {
  width: 560px;
  height: fit-content;
  position: absolute;
  top: 50%;
  left: 70%;
  transform: translate(-70%, -50%);
  border: 1px solid rgba(0, 0, 0, .5);
  padding: 20px;
  border-radius: 15px;
  background: #fff;
  box-shadow: 0 5px 15px rgba(0, 0, 0, .2);
}

.enter-question-form h1 {
  padding-bottom: 10px;
  width: 100%;
  text-align: center;
}
.enter-question-form input
{
  padding: 0 0 0 5px;
}
.enter-question-form label #question {
  margin-left: 5px;
  width: 323px;
  height: 30px;
}

.enter-question-form label #question::placeholder {
  padding-left: 5px;
}

.enter-question-form .answer-group {
  padding: 10px 10px 10px;
  display: flex;
  flex-direction: column;
}

.enter-question-form .answer-group label {
  padding: 5px 0px;
}

.enter-question-form .answer-group label input {
  height: 30px;
  width: 300px;
  margin-left: 10px;
}

.enter-question-form .answer-group label:last-child input {
  width: 273px;
}

.enter-question-form .answer-group label input::placeholder {
  padding-left: 5px;
}

.enter-question-form button[type="submit"] {
  float: right;
  padding: 5px 10px;
  margin-right: 33px;
  background: #29ed71;
  border: 1px solid rgba(0, 0, 0, .5);
  cursor: pointer;
}